@import "../stylesheets/variables.scss";

.header {
  height: 5vh;
  width: 100%;
  z-index: 99;
}

.header__menu {
  padding: 2.5vh 1.5vw;
  height: 3vh;
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  z-index: 9;
  &-lang {
    background: transparent;
    border: none;
    text-transform: uppercase;
    font-size: 2.8vh;
    color: $color-secondary;
    text-decoration: underline;
    transition: all ease 1s;
    outline: none;
    cursor: hand;
    cursor: pointer;
    &:hover {
      transform: scale(1.2);
    }
    &ACC {
      color: #4c7272;
      &:hover {
        transform: scale(1.2);
        color: #364a4e;
      }
    }
  }
  &-acc {
    background-image: url("../images/toggle-off-solid.svg");
    background-color: transparent;
    background-repeat: no-repeat;
    background-size: contain;
    height: 3vh;
    width: 4vh;
    outline: none;
    border: none;
    cursor: hand;
    cursor: pointer;
    &Active {
      background-image: url("../images/toggle-on-solid.svg");
      background-color: transparent;
      background-repeat: no-repeat;
      background-size: contain;
      height: 3vh;
      width: 4vh;
      outline: none;
      border: none;
      cursor: hand;
      cursor: pointer;
    }
  }
}
